<style>.container {
  position: relative;
  display: flex;
  align-items: center;
  background-color: #fff;
  border-radius: 9999px;
  border-width: 2px;
  border-color: #bee3f8;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.container:focus-within {
  outline: 0;
  border-color: #63b3ed;
}

input {
  width: 100%;
  border-style: none;
  border-radius: 0.25rem;
  color: #2c5282;
  background-color: transparent;
  letter-spacing: -0.025em;
  padding: 0.25rem;
  padding-left: 3rem;
  padding-right: 3rem;
  font-weight: 700;
  font-size: 1.875rem;
  line-height: 1;
  text-align: center;
